.text-center {
  text-align: center;
}

.bg-primary {
  background-color: #84d9bc;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.site-header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  padding: 5rem 0;
}
.site-header .logo {
  max-width: 20rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .site-header .logo {
    margin: 0;
  }
}
.site-header .header-content {
  padding: 10rem 0;
}
@media (min-width: 1024px) {
  .site-header .header-content {
    padding: 15rem 0;
  }
}
.site-header .header-content h1 {
  color: #FFFFFF;
  font-size: 4.5rem;
}
@media (min-width: 768px) {
  .site-header .header-content h1 {
    font-size: 6rem;
    max-width: 50%;
  }
}
@media (min-width: 1024px) {
  .site-header .header-content h1 {
    max-width: 40%;
  }
}

.button-primary {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 300ms ease;
  border: none;
}
.button-primary:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .button-primary {
    width: auto;
    display: inline-block;
  }
}